@import 'vars';
@import 'mixins_n_extends';

#root_lightbox {
    @extend .all_zero;
    background-color: rgba(0, 0, 0, 0.6);
    position: fixed;
    z-index: 11;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    box-sizing: border-box;

    #close_lightbox {
        @extend .all_zero;
        position: absolute;
        cursor: pointer;
        z-index: 10;
    }
    
    /* INPUT AREA */

    .input_text_margin_bottom {
        margin-bottom: 10px;
    }

    input {
        @extend .input_small;
    }

    input, button {
        &:not(:first-child) {
            margin-top: 10px;
        }
    }

    button {
        border-radius: 4px;
        width: 100%;
        outline: none;
        border: 2px solid rgba(255, 255, 255, 0);
        margin-bottom: 10px;
        &:focus {
            border: 2px solid $sky_blue;
            border-radius: 4px;
            outline: none;
        }
    }

    .link_section {
        text-align: center;
        padding-bottom: 1px;

        .lightbox_link {
            color: $deep_sky_blue;
            font-size: 13px;
            &:hover {
                text-decoration: underline;
            }
        }
    }

     #lightbox.my_account_lightbox {
         .blue_button {
            margin-top: 20px;
         }
     }

    #lightbox.wide_lightbox {
        width: 600px;

        @media (max-width: 640px) {
            width: 100%;
        }
        
        #lightbox_wrap {

            @media (max-width: 640px) {
                padding: 10px;
            }

            #lightbox_header {
                position: relative;

                #lightbox_header_text {
                    font-weight: $medium;
                    float: left;
                    padding: 15px 60px 16px 20px;

                    .title {
                        opacity: .7;
                        font-weight: $bold;
                        font-size: 16px;
                    }

                    .title_description {
                        margin-top: 3px;
                    }

                }

                #lightbox_close {
                    height: 54px;
                    border-radius: 0 4px 0 4px;
                    position: absolute;
                    float: none;
                    right: 0;
                }
                
                .header_row {
                    padding: 0 20px 0 10px;
                    top: 54px;
                    cursor: pointer;
                    margin-top: 1px;

                    .tab_button {
                        float: left;

                        @media (max-width: 390px) {
                            float: none;    
                        }

                        position: relative;
                        .tab_button_text {
                            font-size: 14px;
                            padding: 10px;
                            opacity: 0.7;
                        }
                        .blue_bottom_line {
                            width: 0%;
                            left: 50%;
                            height: 2px;
                            position: absolute;
                            background: $deep_sky_blue;
                            transition: all 300ms cubic-bezier(.45,.78,0,1);
                                
                            @include tablet {
                                transition: none;
                            }

                            margin-top: -2px;
                        }

                        &:hover {
                            .blue_bottom_line {
                                width: 100%;
                                left: 0%;  
                                @include tablet {
                                    transition: none;
                                        
                                    width: auto;
                                    left: initial;  
                                }
                            }
                        }
                    }

                    .tab_button.active {
                        .tab_button_text {
                            opacity: 1;
                        }
                        
                        .blue_bottom_line {
                            width: 100%;
                            left: 0%;
                            height: 2px;
                            background: $deep_sky_blue;
                        }
                    }

                    .blue_button_wrap {
                        float: right;
                        .blue_button {
                            padding-left: 10px;
                            padding-right: 10px;
                            border-radius: 4px;
                            position: relative;
                        }
                    }

                }
                
            }

        }
        
        .blue_button_wrap.mobile {
            padding: 20px;
            background: $white;
            border-radius: 0 0 4px 4px;

            .blue_button {
                border-radius: 4px;
            }
        }

    }
    

    .lightbox_section.has_table {
        padding: 0 !important;
        text-align: center;
        max-height: 410px;
        overflow-y: auto;

        @media (max-width: 640px) {
            max-height: 327px;
        }

        table {

            tr:first-child {
                height: 30px;
            }

            tr:not(:first-child):not(:last-child) {
                border-bottom: 1px solid $lightest_gray;
            }
            
            th {
                background: $medium_gray;
            }

            th, td {
                padding-left: 10px;
                padding-right: 10px;
                
                &:first-child {
                    padding-left: 20px;
                    padding-right: 10px;
                    text-align: left;
                } 
                
                &:nth-child(2) {
                    width: 72px;
                }
                
                &:nth-child(3) {
                    width: 80px;
                }

                &:last-child {
                    padding-right: 20px;
                } 
            }

            td {
                padding: 20px 0;

                &:first-child {
                    font-weight: $bold;
                }

            }

        }

    }

    
    .lightbox_section.user_list {
        padding: 0 !important;
        text-align: center;
        max-height: 381px;
        overflow-y: auto;
        
        @media (max-width: 640px) {
            max-height: 279px;
        }

        .category_title {
            padding: 0 20px;
            display: table;
            height: 30px;
            background: $medium_gray;
            width: 100%;
            text-align: left;
            box-sizing: border-box;
        }

        table {
            margin: 19px 0;

            th, td {
                padding-left: 10px;
                padding-right: 10px;

                &:first-child {
                    padding-left: 20px;
                    padding-right: 10px;
                    text-align: left;
                    

                    .profile_image_wrapper {
                        margin-right: 20px;
                    }
                }

                &:nth-child(2) {
                    width: 100px;
                }

                &:nth-child(3) {
                    width: 40px;
                    padding-right: 16px;
                    padding-left: 6px;
                }
            }

        }

    }

    .lightbox_section {
        margin-bottom: 4px;
    }

    .content_inside_wrap {
        margin-top: 20px;
        display: table;

        @include mobile {
            width: 100%;
        }
        .text {
        }
        .button_wrap {
            text-align: right;
            padding-left: 21px;
            
            @include mobile {
                padding-left: 10px;
            }

            button {
                display: inline-block;
                width: auto;
                margin-left: 17px;
            }
        }
    }

    .content_inside_footer {
        margin-top: 15px;
        display: table;
        width: 100%;

        .toggle_wrap {
            text-align: right;
            padding-left: 10px;
        }
    }

    /* ID SIGNING LIGHTBOX */
    #lightbox.id_signing {


        #lightbox_wrap {

            @media (max-width: 640px) {
                padding: 10px;
            }

            #lightbox_header {

                .voting_tag_padding {

                    padding: 0 20px 20px 20px;

                    .voting_tag {
                        width: 100%;
                        height: 40px;
                        display: table;
                        background: $aqua_marine;
                        border-radius: 4px;
                        padding: 0 10px;
                        box-sizing: border-box;
                        border: solid 1px $aqua_marine_border;
                    }
                }
            }

            #lightbox_content {
                padding: 34px 0 10px 0;
                position: relative;
                
                @media (max-width: 550px) {
                    padding: 30px 0 0 0;
                }

                .v_line {
                    position: absolute;
                    width: 1px;
                    top: 20px;
                    bottom: 20px;
                    left: 50%;

                    @media (max-width: 550px) {
                        display: none;
                    }
                }

                .line_wrap {
                    padding: 0 20px;
                    margin-bottom: 30px;
                    display: none;

                    @media (max-width: 550px) {
                        display: block;
                    }
                }


                .sign_in_section {
                    float: left;
                    width: 50%;
                    padding: 0 20px 0px 20px;
                    box-sizing: border-box;
                    position: relative;
                    text-align: center;
                    height: 100%;
                    

                    @media (max-width: 550px) {
                        width: 100%;
                        float: none;
                    }

                    .blue_button {
                        border-radius: 4px;
                        
                        @media (max-width: 550px) {
                            margin-bottom: 20px;
                            margin-top: 10px;
                        }

                    }

                    .code {
                        font-weight: $bold;
                        font-size: 16px;
                        margin-bottom: 8px;
                        margin-top: 10px;
                    }

                    .code_text {
                        padding: 0 20px;
                        -webkit-font-smoothing: antialiased;
                    }

                    .sprite_big {
                        margin: 0 auto 45px auto;
                        
                        @media (max-width: 550px) {
                            margin-bottom: 20px;
                        }
                    }
                }

            }

        }
    }

    
    /* DELETE TOPIC LIGHTBOX */
    #lightbox.id_signing.delete_topic {

        #lightbox_header {
            padding: 20px 20px 22px 20px;
            text-align: center;
            color: $pinky_red !important;
            
            .sprite {
                margin: 0 auto 16px auto;
            }
            .delete_title {
                font-size: 22px;
                font-weight: $bold;
                margin-bottom: 5px;
            }
            .delete_description {
                font-size: 14px;
            }
        }

        #lightbox_content {
            padding: 40px 20px !important;
            
            @media (max-width: 750px) {
                padding-top: 30px !important;
            }

            #delete_info_wrap {
                width: 410px;
                margin: 0 auto;
                
                @media (max-width: 470px) {
                    width: 100%;
                }

                .delete_info_text_wrap {
                    margin-bottom: 40px;

                    @media (max-width: 750px) {
                        margin-bottom: 25px;
                    }

                    .delete_info {
                        margin-bottom: 10px;
                        display: table;
                        .sprite-checkmark2x {
                            display: table-cell;
                            float: left;
                            margin-bottom: -1px;
                        }
                        .delete_info_text {
                            display: table-cell;
                            padding-left: 10px;
                        }
                    }
                }

                .delete_info_button_wrap {
                    display: table;
                    width: 100%;

                    .table_cell:first-child {
                        padding-right: 5px;
                    }
                    .table_cell:last-child {
                        padding-left: 5px;
                    }
                }

            }

        }

    }

    #lightbox {
        z-index: 11;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-right: -50%;
        -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        width: 320px;

        @media (max-height: 800px) {
            top: 30px;
            -webkit-transform: translate(-50%, 0);
            -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
        }

        @media (max-width: 800px) {
            top: 10px;
            -webkit-transform: translate(-50%, 0);
            -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
        }

        @include mobile {
            width: 100%;
            top: 0;
        }

        #lightbox_wrap {

            #lightbox_close {
                width: 54px;
                height: 100%;
                display: table;
                float: right;
                cursor: pointer;
                border-radius: 0 4px 0 0;

                &:hover {
                    background: $light_gray;
                }

                .sprite {
                    margin: 0 auto;
                }

            }

            @include mobile {
                padding: 10px;
            }

            #lightbox_header {
                width: 100%;
                height: 54px;
                background: $white;
                border-radius: 4px 4px 0 0;
                display: table;
                box-sizing: border-box;
                color: $medium_black;

                #lightbox_header_text {
                    @extend .table_cell;
                    font-weight: $bold;
                    font-size: 16px;
                    padding: 15px 0 15px 20px;
                    box-sizing: border-box;
                }
            }

            #lightbox_content {
                background: $light_gray;
                font-size: 14px;
                border-radius: 0 0 4px 4px;
                overflow: hidden;

                @media (max-height: 800px) {
                    margin-bottom: 30px;
                }

                #image_edit_section {
                    display: table;
                    width: 100%;
                    margin-bottom: 20px;
                    
                    .profile_image_wrapper {
                        margin-right: 12px;
                    }
                
                    #edit_image_properties {
                        @extend .table_cell;

                        .blue_link {
                            display: inline-block;
                            margin-bottom: 3px;
                        }

                        span {
                            opacity: .69;
                        }

                    }
                }



                .lightbox_section {
                    padding: 20px;

                    /* SOCIAL AREA */

                    .social_button {
                        height: 40px;
                        color: $white;
                        width: 100%;
                        border-radius: 2px;
                        cursor: pointer;

                        &:not(:last-child) {
                            margin-bottom: 10px;
                        }

                        .social_icon {
                            height: 40px;
                            width: 30px;
                            display: table;
                            border-radius: 4px 0 0 4px;
                            position: absolute;

                            .sprite {
                                margin: 0 auto;
                            }
                        }
                        .social_text {
                            padding-left: 30px;
                            box-sizing: border-box;
                            width: 100%;
                            display: table;

                            .table_cell {
                                width: 100%;
                                height: 40px;
                                border-radius: 0 4px 4px 0;
                                padding-left: 10px;
                            }
                        }
                    }

                    #facebook {
                        .social_text .table_cell {
                            background: $facebook_medium;
                        }
                        .social_icon {
                            background: $facebook_dark;
                        }
                    }

                    #facebook:hover {
                        .social_text .table_cell {
                            background: $facebook_light;
                            @include tablet {
                                background: $facebook_medium;
                            }
                        }
                        .social_icon {
                            background: $facebook_medium;
                            @include tablet {
                                background: $facebook_dark;
                            }
                        }
                    }

                    #google {
                        .social_text .table_cell {
                            background: $google_medium;
                        }
                        .social_icon {
                            background: $google_dark;
                        }
                    }

                    #google:hover {
                        .social_text .table_cell {
                            background: $google_light;
                            @include tablet {
                                background: $google_medium;
                            }
                        }
                        .social_icon {
                            background: $google_medium;
                            @include tablet {
                                background: $google_dark;
                            }
                        }
                    }

                    #id_card {
                        .social_text .table_cell {
                            background: $id_medium;
                            color: $medium_black;
                        }
                        .social_icon {
                            background: $id_dark;
                        }
                    }

                    #id_card:hover {
                        .social_text .table_cell {
                            background: $id_light;
                            @include tablet {
                                background: $id_medium;
                            }
                        }
                        .social_icon {
                            background: $id_medium;
                            @include tablet {
                                background: $id_dark;
                            }
                        }
                    }

                }

            }

        }
    }
}

#root_lightbox.with_save_button {
     @media (max-width: 640px) {
        #lightbox_content {
            margin-bottom: 0 !important;
            border-radius: 0 !important;
        }
    }
}